@import '../../global.scss';

.portfolio {
	background-color: #fff;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding-top: 80px;

	ul {
		margin: 12px;
		padding: 0;
		list-style: none;
		display: flex;

		@include mobile {
			margin: 10px 0;
			flex-wrap: wrap;
			justify-content: center;
		}
	}

	.container {
		max-width: 1024px;
		min-height: 400px;

		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		justify-content: center;

		.item {
			width: 280px;
			height: 160px;
			border-radius: 20px;
			border: 1px solid rgb(240, 239, 239);
			margin: 10px 20px;

			display: flex;
			align-items: center;
			justify-content: center;
			color: #fff;

			position: relative;
			transition: all 0.5s ease;
			cursor: pointer;

			h3 {
				position: absolute;
				font-size: 20px;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				z-index: 1;
			}

			&:hover {
				background-color: $mainColor;
				img {
					opacity: 0.2;
					z-index: 0;
				}
			}
		}
	}
}
