@import '../../global.scss';

.intro {
	background-color: #fff;
	display: flex;
	padding: 80px 0;
	width: 100%;
	height: $height;

	@include mobile {
		flex-direction: column;
		align-items: center;
	}

	@include tablet {
		flex-direction: column;
		align-items: center;
		justify-content: space-around;		
	}

	.left {
		flex: 0.5;
		overflow: hidden;	

		@include mobile {
			flex: 1;
		}

		@include tablet {
			flex: 1;
		}

		.img-container {
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;

			@include tablet {					
				display: flex;
				justify-content: center;							
			}

			img {
				height: 80%;
				border-radius: 10px;
				
				@include mobile {
					height: 90%;										
				}				
			}
		}
	}

	.right {
		flex: 0.5;
		position: relative;

		@include mobile {
			flex: 1;
		}

		@include tablet {
			flex: 1;
		}

		.wrapper {
			width: 100%;
			height: 100%;
			padding-left: 10vh;
			display: flex;
			flex-direction: column;
			justify-content: center;

			@include mobile {
				padding-left: 0;
				align-items: center;
			}

			@include tablet {
				padding-left: 0;
				align-items: center;
			}

			h1 {
				font-size: 60px;
				margin: 10px 0;

				@include mobile {
					font-size: 40px;
				}
			}

			h2 {
				font-size: 35px;

				@include mobile {
					font-size: 30px;
				}
			}

			h3 {
				font-size: 30px;

				@include mobile {
					font-size: 20px;
				}
			}

			span {
				font-size: 30px;
				color: crimson;

				@include mobile {
					font-size: 20px;
				}
			}
		}		
	}
}
